import { AxiosResponse } from 'axios';
import { requestAdapterProvider } from './request_adapter/request_adapter';
import { errorHandlerProvider } from './error_handler/error_handler';

export class Interceptor {
    public handle(response: AxiosResponse): AxiosResponse {
        if (response.data?.errorCode) {
            return errorHandlerProvider.handle(response);
        }

        return requestAdapterProvider.handle(response);
    }
}

export const interceptor = new Interceptor();
